.dialogs {
    display: grid;
    grid-template-columns: 1fr 3fr;
    grid-template-rows: 3fr 1fr;
    grid-gap: 16px;
    grid-template-areas:
        "dialog message"
        "dialog newMessage";
}

.dialog {
    grid-area: dialog;
}

.message {
    grid-area: message;
}

.newMessage {
    grid-area: newMessage
}